import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '@/components/HelloWorld';
import Student from '@/components/Student';
import About from '@/components/About';

Vue.use(Router);

export default new Router({
  routes: [{
      path: '/home',
      name: 'HelloWorld',
      component: HelloWorld,
    },
    {
      path: '/home/student',
      name: 'Student',
      component: Student,
    },
    {
      path: '/home/about',
      name: 'About',
      component: About,
    },
  ],
});



