<template>
  <div class="hello">
    <p id="trigger2">全班學生</p>
    <draggable
      v-model="stuList"
      class="elBox"
      group="people"
      @start="drag = true"
      @end="drag = false"
      handle=".handle"
    >
      <div class="dragEl" v-for="element in stuList" :key="element.id">
        <span class="control handle"></span>
        {{ element.name }}
      </div>
    </draggable>
    <div
      class="group_box"
      v-for="(item, groupIndex) in groupArr"
      :key="groupIndex"
    >
      <div class="group">
        <div class="close" @click="deleteGroup(groupIndex)"></div>
        <p>組別{{ groupIndex + 1 }}</p>
        <draggable
          v-model="item.stu"
          class="elBox"
          group="people"
          @start="drag = true"
          @end="drag = false"
        >
          <div
            class="dragEl"
            v-for="(element, index) in item.stu"
            :key="element.id"
            @click="setLeader(groupIndex, element.stuId)"
            :class="{ leader: Number(item.leader) == Number(element.stuId) }"
          >
            <span class="control handle"></span>
            {{ element.name }}
          </div>
        </draggable>
      </div>
    </div>
    <div class="btn" @click="add">新增</div>
  </div>
</template>

<script>
import draggable from "vuedraggable";

export default {
  name: "HelloWorld",
  components: {
    draggable
  },
  data() {
    return {
      stuList: [
        { name: "A", stuId: "01" },
        { name: "B", stuId: "02" },
        { name: "C", stuId: "03" },
        { name: "D", stuId: "04" },
        { name: "E", stuId: "05" },
        { name: "F", stuId: "06" },
        { name: "G", stuId: "07" },
        { name: "H", stuId: "08" },
        { name: "I", stuId: "09" },
        { name: "J", stuId: "10" }
      ],
      groupArr: []
    };
  },
  methods: {
    add() {
      this.groupArr.push({
        stu: []
      });
    },
    setLeader(groupIndex, id) {
      // this.groupArr[groupIndex].leader = id;
      this.$set(this.groupArr[groupIndex], "leader", id);
    },
    deleteGroup(index) {
      this.groupArr[index].stu.forEach(element => {
        console.log(element);
        this.stuList.push(element);
      });
      // this.groupArr[index]
      // this.stuList.push(this.groupArr[index]);
      this.groupArr.splice(index, 1);
    },
    initAnimate() {
      const scene2 = this.$scrollmagic
        .scene({
          triggerElement: "#trigger2",
          triggerHook: 0.5,
          duration: 300
        })
        .setTween("#trigger2", {
          css: {
            borderTop: "30px solid white",
            backgroundColor: "blue",
            scale: 0.7
          }
        })
        .addIndicators({ name: "title" });
      this.$scrollmagic.addScene(scene2);
    }
  },
  mounted() {
    // this.initAnimate();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.sortable-ghost {
  opacity: 0.5;
}
.hello {
  width: 90%;
  margin: auto;
}
.group {
  position: relative;
  .close {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #ccc;
    position: absolute;
    top: 0;
    right: 0;
    transform: rotate(45deg);
    &:before {
      width: 4px;
      height: 60%;
    }
    &:after {
      width: 60%;
      height: 4px;
    }
    &:before,
    &:after {
      content: "";
      display: block;
      background-color: #fff;
      border-radius: 2px;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
    }
  }
}
.elBox {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  border: 1px solid #ddd;
  .dragEl {
    width: calc(25% - 20px);
    font-size: 20px;
    line-height: 40px;
    background-color: #ccc;
    margin: 10px;
    position: relative;
    &.leader {
      background-color: #faa;
    }
    .control {
      width: 30px;
      height: 30px;
      font-size: 20px;
      display: inline-block;
      cursor: pointer;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 10px;
      margin: auto;
      &::before {
        content: "";
        display: block;
        width: 80%;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        box-shadow: 0 5px 0px #fff, 0 -5px 0px #fff;
      }
    }
    @media (max-width: 768px) {
      width: calc(50% - 20px);
    }
  }
}
.btn {
  font-size: 20px;
  padding: 5px 10px;
  margin: 10px;
  border: 1px solid #ccc;
  color: #666;
  display: inline-block;
  cursor: pointer;
  transition: 0.3s;
  &:hover {
    background-color: #ccc;
    color: #fff;
  }
}

h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



