<template lang="pug">
.main
  h1 list
  draggable.list-group(
    tag='ul'
    v-model='list' 
    v-bind='dragOptions' 
    @start='drag = true' 
    @end='drag = false'
    )
    transition-group(
      type='transition'
      :name="!drag ? 'flip-list' : null"
      )
      li.list-group-item(
        v-for='element in list'
        :key='element.order'
        )
        i(
          :class="element.fixed ? 'fa fa-anchor' : 'glyphicon glyphicon-pushpin'"
          @click='element.fixed = !element.fixed'
          aria-hidden='true'
          )
          | {{ element.name }}

</template>

<script>
import draggable from "vuedraggable";

const message = [
  "vue.draggable",
  "draggable",
  "component",
  "for",
  "vue.js 2.0",
  "based",
  "on",
  "Sortablejs"
];
export default {
  components: {
    draggable
  },
  data() {
    return {
      list: message.map((name, index) => {
        return { name, order: index + 1 };
      }),
      drag: false
    };
  },
  methods: {
    sort() {
      this.list = this.list.sort((a, b) => a.order - b.order);
    }
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.button
  margin-top: 35px

.flip-list-move
  transition: transform 0.5s

.no-move
  transition: transform 0s

.ghost
  opacity: 0.5
  background: #c8ebfb

.list-group
  min-height: 20px

.list-group-item
  cursor: move

.list-group-item i
  cursor: pointer

.list-group
  width: 200px
  margin: auto
  .list-group-item
    width: 100%
    line-height: 30px
    background-color: #fff
    border: 1px solid #ddd

h1,
h2
  font-weight: normal

ul
  list-style-type: none
  padding: 0

li
  display: inline-block
  margin: 0 10px

a
  color: #42b983
</style>



